import Vue from 'vue';
import Router from 'vue-router';
const MyRecord = () => import('components/MyRecord');
const Log = () => import('components/Log');
const Mycal = () => import('components/Mycal');
const SIX = () => import('components/SIX');
const Record6 = () => import('components/Record6');
const Chart2 = () => import('components/Chart2');
const ChartFD = () => import('components/ChartFD');

Vue.use(Router);

export default new Router({
  routes: [
    // {
    //   path: '/hello',
    //   name: 'Hello',
    //   component: Hello
    // },
    {
      path: '/myrecord',
      name: 'MyRecord',
      component: MyRecord
    },
    {
      path: '/log',
      name: 'Log',
      component: Log
    },
    {
      path: '/',
      name: 'Mycal',
      component: Mycal
    },
    // {
    //   path: '/myCharts',
    //   name: 'MyCharts',
    //   component: MyCharts
    // },
    // {
    //   path: '/mytest',
    //   name: 'MyTest',
    //   component: MyTest
    // },
    {
      path: '/six',
      name: 'SIX',
      component: SIX
    },
    {
      path: '/record6',
      name: 'Record6',
      component: Record6
    },
    {
      path: '/chart2',
      name: 'Chart2',
      component: Chart2
    },
    {
      path: '/fd',
      name: 'ChartFD',
      component: ChartFD
    }
  ]
});



